input {
    font: inherit;
    padding: var(--size-medium);
    width: 80%;
    border-radius: var(--size-tiny);
    font-size: var(--size-medium);
    border-width: 1px;
    margin-top: var(--size-small);
}

.input-wrapper {
    display: flex;
    flex-flow: column;
    align-items: start;
    font-size: var(--size-large);
}