.tech-icon {
    width: var(--size-epic);
    height: var(--size-epic);
    padding-top: var(--size-big);
}

.tech-card {
    background-color: transparent;
    width: 175px;
    height: 200px;
    perspective: 1000px;
    font-size: var(--size-largeish);
    font-weight: bolder;
    padding: var(--size-tiny);
    
  }
  
  .tech-card-inner {
    position: relative;
    width: 100%;
    height: 100%;
    text-align: center;
    transition: transform 0.6s;
    transform-style: preserve-3d;
    box-shadow: 0 var(--size-micro) var(--size-tiny) rgba(0,0,0,.16);
  }
  
  .tech-card:hover .tech-card-inner {
    transform: rotateY(180deg);
  }
  
  .tech-card-front, .tech-card-back {
    position: absolute;
    width: 100%;
    height: 100%;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
    border-radius: var(--size-small);
  }
  
  .tech-card-front {
    background-color:white;
    color: black;
  }
  
  .tech-card-back {
    background-color: white;
    color: black;
    transform: rotateY(180deg);
  }

  .card-description {
    font-size: var(--size-medium-plus);
    padding: var(--size-small);
    font-weight: lighter;
  }

  .card-title {
      padding-top: var(--size-tiny);
  }

.card-group {
  display: flex;
  justify-content: center;
  width: 100%; 
}

.wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin-bottom: var(--size-huge);
  max-width: 1200px;
}

@media (min-width: 720px) {
    .tech-card {
        padding: var(--size-medium) var(--size-large);
    }
}