.model-card {
    position: relative;
    background-color: white;
    width: 200px;
    height: 120px;
    border-radius: var(--size-small);
    left: 40vw;
    top: 40vh;
}

.model-background {
    height: 100%;
    width: 100%;
    background-color: black;
    overflow: hidden;
    position: fixed;
    opacity: 0.5;
    animation: hideMe 0s ease-in 2s forwards;
}

.check-svg {
    width: 80px;
    height: 80px;
    position: relative;
    margin-left: var(--size-small);
}

@media (max-width: 720px) {
    .model-card {
        left: 26vw;
    }
    .check-svg {
        margin-left: var(--size-large);
    }
}

.notification {
    animation: hideMe 0s ease-in 2s forwards;
}

@keyframes hideMe {
    to {
        width:0;
        height:0;
        overflow:hidden;
    }
}
