.nav-container {
    display: flex;
    width: 100%;
    justify-content: space-around;
    align-items: center;
    background: linear-gradient(185deg, rgba(2,1,6,0.38279061624649857) 0%, rgba(0,212,255,0) 58%);
}

.nav-links {
    display: flex;
    overflow: hidden;
    align-items: baseline;
    width: 400px;
    justify-content: space-around;
}

.nav-links > * {
    text-decoration: none;
}

@media (max-width: 1440px) {
    .nav-links > * {
        margin-left: var(--size-medium);
    }
}

@media (max-width: 720px) {
    .nav-container {
        z-index: 1;
        position: fixed;
        height: var(--size-huge);
        bottom: 0;
        right: 0;
        left: 0;
        margin-right: 0;
        border-top: 0.5px solid rgb(139, 128, 128);
        background-color: #e8e8ee;
        overflow: hidden;
    }
}

.nav-icon {
    width: var(--size-larger);
}

.nav-label {
    display: block;
    font-size: var(--size-large);
    color: black;
}

.name {
    margin-top: var(--size-big);
    margin-bottom: var(--size-bigger);
    font-weight: bold;
    font-size: var(--size-bigger);
    line-height: 1;
}

.jay {
    font-size: var(--size-huge);
    color: #49498a;
    float: left;
    font-size: 75px;
    line-height: 60px;
}


.location {
    font-size: var(--size-medium);
    display: flex;
    padding-left: var(--size-small);
    text-decoration: overline;
}

@media (max-width: 720px) {
    .name {
        display: none;
    }
}

