.overflow-contact {
    overflow-x: hidden;
    height: 110vh;
}

.page-header {
    display: flex;
    margin-left: 5vw;
    font-size: var(--size-big);
    margin-top: var(--size-bigger);
    margin-bottom: var(--size-bigger);
}

.form {
    position: absolute;
    width: 100vw;
}

.fields {
    padding: var(--size-huge);
    background-color: #e6e6ee;
    border-radius: var(--size-small);
    box-shadow: 0 var(--size-micro) var(--size-tiny) rgba(0,0,0,.16);
    border-color: lightgrey;
    margin-left: 5vw;
    margin-right: 5vw;
}

@media (min-width: 720px) {
    .fields {
        margin-right: 20vw;
        margin-left: 10vw;
    }
    .page-header {
        margin-left: 10vw;
    }
}

@media (max-width: 720px) {
    .fields {
        padding: var(--size-large);
    }
}

.send {
    display: flex;
}

.message {
    resize: none;
    font: inherit;
    padding: var(--size-small);
    width: 90%;
    height: 200px;
    font-size: var(--size-medium);
    margin-top: var(--size-small);
    border-radius: var(--size-tiny);
}

.required {
    color: red;
    display: contents;
}

.button-wrapper {
    display: flex;
    font-size: var(--size-medium);
    align-items: center;
}
